import * as React from "react";
import Navigation from "src/components/Navigation/Navigation";
import styled from "styled-components";

export interface VideoProps {}

const Content = styled.div`
    width: 100%;
    padding-bottom: 200px;
    font-size: 14px;

    min-height: 100vh;
`;

const Desc = styled.div`
    padding: 0 15%;
    margin-top: 62px;
    & > div {
        display: flex;
        margin-bottom: 34px;
        & > div:nth-child(1) {
            width: calc(20% - 45px);
            margin-right: 45px;
            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.61;
        }
        & > div:nth-child(2) {
            width: 80%;
            display: flex;
            flex-wrap: wrap;
        }
    }
`;
export default class Video extends React.Component<VideoProps, any> {
    public render() {
        return (
            <>
                <Navigation
                    list={[
                        { name: "홈", link: "/" },
                        { name: "홍보", link: "/promotion" },
                        { name: "브랜드 영상", link: "/promotion/video" }
                    ]}
                />
                <Content>
                    <Desc>
                        <div>
                            <div>브랜드 영상</div>
                            <div>
                                {/* kMb-pBDcSoc */}
                                <iframe width="420" height="315" src="https://www.youtube.com/embed/kMb-pBDcSoc" />
                            </div>
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



